if ("requestIdleCallback" in window) {
  requestIdleCallback(() => {
    import("@spectrum-web-components/bundle/elements.js");
    import("@spectrum-web-components/grid/sp-grid.js");
  });
} else {
  requestAnimationFrame(() => {
    import("@spectrum-web-components/bundle/elements.js");
    import("@spectrum-web-components/grid/sp-grid.js");
  });
}
window.Overlay = Overlay;
var StyledElement = class extends HTMLElement {
  constructor() {
    super();
    this.attachShadow({ mode: "open" });
    this.shadowRoot.innerHTML = `
            <style>
                :host {
                    display: block;
                    background-color: var(--spectrum-global-color-gray-50);
                    color: var(--spectrum-global-color-gray-800);
                    border: 1px solid;
                    padding: 2em;
                }
            </style>
            <slot></slot>
        `;
  }
};
customElements.define("styled-element", StyledElement);
document.querySelector('sp-tab-panel[value="api"]')?.addEventListener("click", (event) => {
  const path = event.composedPath();
  const row = path.find(
    (el) => el.localName === "sp-table-row" && el.id
  );
  if (row) {
    location.hash = row.id;
    event.target.dispatchEvent(
      new CustomEvent("copy-text", {
        bubbles: true,
        composed: true,
        detail: {
          text: row.dataset.value,
          message: `${row.dataset.name} copied to clipboard!`
        }
      })
    );
  }
});
